body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
*:focus {
  outline: 0 !important;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.main {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-wrap: wrap;
  background-color: rgb(32, 32, 32);
  min-height: 200vh;
  
  padding: 50px;

}

.child {
  max-width: 200px;
  
  text-align: left;
  padding: 50px;
  
  background-color: rgb(200, 231, 200);

}

*{
    font-family: 'Courier New', Courier, monospace;
}
Link {
    cursor: pointer;
}
.mainHeader {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2em;
    z-index: 2;
    color: rgb(22, 22, 24);
    background-color: white;
    box-shadow: 1em 1em  rgb(22, 22, 24);
    border-radius: 14em 15em 5em 5em;
    font-family: 'Courier New', Courier, monospace;
}
.PC {
    transform: rotate(10deg);
    fill: rgb(22, 22, 24);
    stroke: none;
}
.Controller {
    transform: rotate(-20deg);
    fill: rgb(22, 22, 24);
    stroke: none;
}
.Headphones {
    transform: rotate(5deg);
    fill: rgb(22, 22, 24);
    stroke: none;
}
.iconsContainer {
    display: flex;
    justify-content: space-around;
    position: sticky;
    position: -webkit-sticky;
    top: 10em;
    z-index: 1;
    right: 10%;
    height: 5em;
    width: 100%;
    height: 100%;
}
.hrTitle {
    border: 2px solid rgb(22, 22, 24);
    color: rgb(22, 22, 24);
    background-color:rgb(22, 22, 24);
    width: 80%;
}
.displayPic {
    background-image: 
    linear-gradient(to bottom, rgba(255, 255, 255, 0.116), rgba(92, 112, 156, 0.664)),
    url(/static/media/cute.7968a62b.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    width: 20em;
    min-height: 20em;
    transition: transform .2s;
    border: .5em solid rgb(22, 22, 24);
    text-align: center;
    vertical-align: bottom;
    border-radius: 50%;
    
}
.displayPic:hover {
    transform: scale(1.1);
    
}

.mainBody {
    align-items: center;
    min-height: 100em;
    display: flex;
    flex-direction: column;
    background-color: #b7ecff;
    justify-content: baseline;
    padding: 2em;
    color: white;
    z-index: 0;
}
.contentBlock { 
    margin: 2em;
    background-color: white;
    width: 75%;
    padding: 2em;
    border: .5em solid rgb(22, 22, 24);
    border-radius: 2em;
    color: rgb(22, 22, 24);
    box-shadow: 3em 3em  rgb(22, 22, 24);
    z-index: 2;
}
.skillBox {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 1em 0em 1em 0em;
    
}
@media (max-width: 80rem) {
    .skillBox {
        flex-direction: column;
    }
    body {
        font-size: 50%;
    }
}
.skillName {
    flex-basis: auto;
    
    
}
.skillGraphic {
    
    min-width: 80%;
    height: 3rem;
    text-align: right;
    border-radius: 1em;
    border:4px solid rgb(22, 22, 24);
    
}
/* IndividualGraphics */
.skillGraphicFill_JS {
    background-color: rgb(255, 255, 64);
    width: 65%;
    height: 100%;
    border-radius: 0.6em 0em 0em .6em;

}
.skillGraphicFill_Java {
    background-color: rgb(255, 59, 59);
    width: 80%;
    height: 100%;
    border-radius: 0.6em 0em 0em .6em;
}
.skillGraphicFill_Python {
    background-color: rgb(144, 158, 238);
    width: 90%;
    height: 100%;
    border-radius: 0.6em 0em 0em .6em;
}
.skillGraphicFill_C {
    background-color: rgb(255, 152, 93);
    width: 80%;
    height: 100%;
    border-radius: 0.6em 0em 0em .6em;
}
.skillGraphicFill_Unix {
    background-color: lightgreen;
    width: 80%;
    height: 100%;
    border-radius: 0.6em 0em 0em .6em;
}
/* Projects */
.projectsBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.projectContainer {
    border: .5em solid rgb(22, 22, 24);
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    width: 14em;
    height: 1em;
    transition: .2s all ease-out;
    text-align: center;
    border-radius: 50%;
    background: linear-gradient(to bottom, transparent 0%, transparent 70%, rgb(36, 49, 65));
    background-size: 400% 400%;
    color:transparent;
    padding: 7em 0;
    z-index: 0;
}
.projectContainer:hover {
    transform: scale(1.1);
    background-position: 100% 100%;
    color:white;
    box-shadow: 0px 0px 20px rgb(0, 204, 255) ;
}
.projectContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 50%;
    mix-blend-mode: multiply;
    z-index: -1;
}
.projectContainer h2{
    z-index: -2;
}
.myButton {
    
    border: none;
    border-radius: 50%;
}


